.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2e6dcc;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  padding-left: 15px;
  font-size: 18px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.photo {
  width: 350px;
  height: 200px;
}

.info {
  gap: 5px;
  display: flex;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

